// Collapsible 
const Collapsible = () => {
  const [activeIndex, setActiveIndex] = useState(null);

  const handleClick = (index) => {
    //       ,   
    setActiveIndex(activeIndex === index ? null : index);
  };

  return (
    <div>
      <button
        type="button"
        className={`collapsible ${activeIndex === 0 ? 'active' : ''}`}
        onClick={() => handleClick(0)}
      >
        
      </button>
      <div
        className="content"
        style={{
          maxHeight: activeIndex === 0 ? '500px' : '0',
          padding: activeIndex === 0 ? '0' : '0',
        }}
      >
          <ul>
            <li><a href="#2Page">&nbsp;:&nbsp;<span>ABOUT</span></a></li>
            <li><a href="#3Page">&nbsp;:&nbsp;<span>PORTFOLIO</span></a></li>
            <li><a href="#4Page">&nbsp;:&nbsp;<span>DESIGN</span></a></li>
            <li><a href="#5Page">&nbsp;:&nbsp;<span>CONTACT</span></a></li>

          </ul>
          
              <button type="button"
                className={`collapsible ${activeIndex === 0 ? 'active' : ''}`}
                onClick={() => handleClick(0)}>
                   X
              </button>
            
      </div>
    </div>
  );
};

